*{font-family: 'Noto Sans KR', sans-serif;}
/* reset */
body,p,h1,h2,h3,h4,h5,h6,ul,ol,li,dt,dd,table,th,td,form,fieldset,legend,input,textarea,button,blockquote{margin:0;padding:0; -webkit-text-size-adjust:none;word-break:break-all;}
body,table{font-size: 14px;font-family: 'Noto Sans KR', sans-serif; color:#666;letter-spacing:0;}
body{background:#fff;-webkit-text-size-adjust:none}
fieldset,img,iframe,frame{border:0}
legend,caption{overflow:hidden;position:absolute;font-size:0;line-height:0;visibility:hidden}
h1,h2,h3,h4,h5,h6{font-size:1em}
img,input,textarea,fieldset{border:0 none;outline:none}
ul,ol,dl,li,dt,dd{list-style:none;margin:0;padding:0;}
em,address{font-style:normal}
a{color:#463959;text-decoration:none;outline: none;}
a:link,a:visited,a:hover,a:active{text-decoration:none;outline: none;}
strong{font-weight:bold}
legend,hr,caption{display:none}
table{border-collapse:collapse;border-spacing:0}
input,textarea,button{}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block;}
select{text-indent:5px;}
figure{margin:0;padding:0;}

/* nav */
.menu_wrap{height:41px; padding-bottom:10px; background:#e8ebf3;}
.menu_wrap .menu{position:relative; height:40px; border-bottom:1px solid #ccc; background:#fff; display:flex;}
.menu_wrap .menu a{width:150px; padding:10px 3px; white-space:nowrap; display:block; position:relative; font-size:14px; font-weight:bold; line-height:20px; text-align:center; color:#89898a;}
.menu_wrap .menu a.active{color:#293FEB; border-bottom:1px solid #293FEB;}
/* btn */
.btn, button{display:inline-block;box-sizing:border-box;border:none;border-radius:0;box-shadow: none;background: none;-webkit-appearance:none;}
.btn strong{font-weight:bold;}
.btn_gray{background:#293FEB;color:#fff;}
.btn_light_gray{background:#a3a0a8;color:#fff;}
.btn_pink{background: #dc0082;color:#fff;}
.btn_b_pink{border:1px solid #293FEB;background:#fff;color:#293FEB;}
.btn_b_white{border:1px solid #fff;background:#dc0082;color:#fff;}
.btn_full_h40{width:100%;height:40px;font-size:14px;line-height:40px;vertical-align: middle;text-align:center;}
.btn_full_h50{width:100%;height:50px;font-size:16px;line-height:50px;vertical-align: middle;text-align:center;}
.btn_h30_p10{height:30px;padding:0 10px;font-size:14px;line-height:30px;}
.btn_arrow{overflow:hidden;width:9px;height:13px;background:url(//img.x1.co.kr/robo/images/shinhan/mobile/common/btn_board_date.png) no-repeat;background-size:19px 13px;color:transparent;}
.btn_arrow.is_prev{background-position:0 0;}
.btn_arrow.is_next{background-position:-10px 0;}
.btn_back{width:40px;height:50px;background:url(//img.x1.co.kr/robo/images/shinhan/mobile/common/ico_back.png) no-repeat 50% 50%;background-size:10px 17px;color:transparent}
.btn_search_pink{width:35px;height:30px;background:url(//img.x1.co.kr/robo/images/shinhan/mobile/common/ico_search_pink.png) no-repeat 50% 50%;background-size:16px 16px;color:transparent}
.btn_wrap{margin-top:15px;}
.btn_multi_wrap{padding:20px 0;display:-webkit-box;display:-webkit-flex;display:-moz-flex;display:flex;}
.btn_multi_wrap .btn{-webkit-box-flex:1;-webkit-flex:1;-moz-flex:1;flex:1;margin-left:10px;}
.btn_multi_wrap .btn:first-child{margin-left:0;}


/* MTS */

.h_search{display:block;position:relative;padding:5px 30px 5px 5px ;background: #fff; width:100%;}
.h_search input{display:block;width:100%;font-size:14px;color:#000;line-height:20px;}
.h_search button{display:block;overflow:hidden;position:absolute;right:10px;top:7px;width:16px;height:16px;background: url(//img.x1.co.kr/robo/images/shinhan/mobile/common/ico_h_search.png) no-repeat 0 0;background-size:16px 16px;color:transparent;}

/*nav*/
.side_container{position:fixed; top:0; left:0; z-index:999; width:100%; height:100%; visibility: visible; transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out; background:rgba(0,0,0,0.5);}
.side_container.hidden{opacity: 0; visibility: hidden;}
.side_nav .inner.hidden{transform:translateX(-100%); transition: 0.3s ease-in-out;}
.side_nav .side_logo{display:block;padding-top:16px;}
.side_nav .side_logo img{width:71px; height:18px;}
.side_nav .inner{position: relative;z-index: 100;background: #fff;width: 270px; height:100vh; transform: translateX(0); transition: 0.3s ease-in-out;  opacity:1;}
.side_nav .inner::-webkit-scrollbar {width: 0;}
.side_nav .nav_top{overflow: hidden;position: relative;height: 50px;padding:0 15px;position: relative;color: #fff;background: #0046ff;}
.nav_top .log_menu{padding-top:10px;font-size:0;}
.log_menu .btn{margin-left:10px;}
.log_menu .btn:first-child{margin-left:0;}
.side_nav .cate_nav{background: #fff;display:block;}
.side_nav .cate_nav ul li a{display: block;background: #fff;line-height: 50px;font-weight: bold;border-bottom: 1px solid #e6e6e6;position: relative;padding-left: 33px;font-size: 14px;color: #666;}
.side_nav .cate_nav ul li a span{position: absolute;right: 15px;top: 18px;width: 7px;height: 13px;background: url(//img.x1.co.kr/robo/images/mobile/common/ico_arw1.png) 0 0 no-repeat;background-size:7px 13px;}
.side_nav .cate_nav ul li a em{position: absolute;left: 10px;top: 16px;}
.side_nav .cate_nav ul li:nth-child(1) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate1.png) 0 0 no-repeat;width: 16px;height: 16px;background-size:16px 16px;}
.side_nav .cate_nav ul li:nth-child(2) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate2.png) 0 0 no-repeat;width: 17px;height: 17px;background-size:17px 17px;}
.side_nav .cate_nav ul li:nth-child(3) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate_quant.png) 0 0 no-repeat;width: 19px;height: 16px;background-size:19px 16px;}
.side_nav .cate_nav ul li:nth-child(4) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate3.png) 0 0 no-repeat;width: 18px;height: 17px;background-size:18px 17px;}
.side_nav .cate_nav ul li:nth-child(5) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate4.png) 0 0 no-repeat;width: 17px;height: 17px;background-size:17px 17px;}
.side_nav .cate_nav ul li:nth-child(6) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate5.png) 0 0 no-repeat;width: 18px;height: 21px;background-size:18px 21px;}
.side_nav .cate_nav ul li:nth-child(7) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate6.png) 0 0 no-repeat;width: 18px;height: 15px;background-size:18px 15px;}
.side_nav .cate_nav ul li:nth-child(8) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate7.png) 0 0 no-repeat;width: 16px;height: 18px;background-size:16px 18px;}
.side_nav .cate_nav ul li:nth-child(9) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate8.png) 0 0 no-repeat;width: 18px;height: 15px;background-size:18px 15px;}
.side_nav .cate_nav ul li:nth-child(10) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate9.png) 0 0 no-repeat;width: 15px;height: 15px;background-size:15px 15px;}
.side_nav .cate_nav ul li .cate_sub{display: none;}
.side_nav .cate_nav ul li .cate_sub p a{display: block;position: relative;color: #333;padding-left: 33px;background: #f5f7fc;font-size: 13px;line-height: 45px;border-bottom: 1px solid #e0e0e0;}
.side_nav .cate_nav ul li.active .cate_sub{display: block;}
.side_nav .cate_nav ul li.arw a span{right: 12px;top: 22px;width: 13px;height: 7px;background: url(//img.x1.co.kr/robo/images/mobile/common/ico_arw2.png) 0 0 no-repeat;background-size:13px 7px;}
.side_nav .cate_nav ul li.active a{color: #dc0082;}
.side_nav .cate_nav ul li.active.arw a span{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_arw3.png) 0 0 no-repeat;background-size:13px 7px;}
.side_nav .cate_nav ul li.active:nth-child(2) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate2_on.png) 0 0 no-repeat;width: 17px;height: 17px;background-size:17px 17px;}
.side_nav .cate_nav ul li.active:nth-child(3) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate_quant_on.png) 0 0 no-repeat;width: 19px;height: 16px;background-size:19px 16px;}
.side_nav .cate_nav ul li.active:nth-child(10) a em{background: url(//img.x1.co.kr/robo/images/mobile/common/ico_cate9_on.png) 0 0 no-repeat;width: 15px;height: 15px;background-size:15px 15px;}
.side_nav .btn_login{padding: 15px;}
.side_nav .btn_login a{display: block;border: 1px solid #dc0082;text-align: center;color: #dc0082;padding: 5px 0;}
.side_nav .btn_login a img{width: 11px;vertical-align: middle;}
.side_nav .btn_close{position: absolute;right: 15px;top: 17px;cursor: pointer;}
.side_nav .btn_close img{width: 15px;}

/*날짜버튼*/
/* 날짜 선택 */
.date_select{padding-top:15px;text-align:center;}
.date_select .ds_view{display:inline-block;position:relative;width:148px;height:28px;margin:0 auto;border:1px solid #293FEB;border-radius:20px;text-align:center;}
.date_select .date{display:inline-block;padding-top:4px;font-size:14px;font-weight:bold;color:#333;line-height:20px;}
.date_select .is_prev{position:absolute;left:15px;top:8px;}
.date_select .is_next{position:absolute;right:15px;top:8px;}

/*모멘텀*/
.cont_last{padding-bottom:50px; background:#fff;}
.cont_inside{padding-left:10px; padding-right:10px;}
.l_table{padding-top:15px; overflow:hidden;}
.row_table{ table-layout:fixed; width:100%; border-top:1px solid #808080;}
.row_table th{padding: 12px 10px; border-bottom: 1px solid #e6e6e6; background: #f5f7fc; font-size: 14px; font-weight: normal; color: #666; line-height: 18px; text-align: left; vertical-align: middle; width:35%;}
.row_table td{padding: 12px 10px; border-bottom: 1px solid #e6e6e6; background: #fff; font-size: 14px; color: #333; line-height: 18px; text-align: left; vertical-align: middle; width:65%;}

/*리포트*/
.link_board_list{margin-top:15px; border-top:1px solid #808080;}
.link_board_list li{border-bottom:1px solid #e6e6e6; display:block; padding:11px 10px;}
.board_title{display:block; font-size:14px; font-weight:bold; color:#000; line-height:22px;}
.board_overtxt{display:block; overflow:hidden; font-size:13px; color:#666; line-height:22px; white-space:no-wrap; text-overflow:ellipsis;}
.board_info{display:block; margin-top:4px; font-size:0;}
.board_info span{display:inline-block; position:relative; font-size:12px; color:#666; line-height:20px;}

/*급등주*/
table .t_up{color: #fd5758;}

/*rs스코어*/
.tab_list{overflow:hidden;}
.tl_2 li{width:50%;}
.tab_list li:first-child{border-left:none;}
.tab_list li{float:left; box-sizing:border-box; background:#fff; box-sizing:border-box; padding:10px 0; border-top:1px solid #ccc; font-size:12px; font-weight:bold; line-height:20px; text-align:center; vertical-align:middle; border-bottom:1px solid #333; border-left:1px solid #333;}
.tab_list li.active{position:relative; border-bottom:none; color:#293FEB;}
.tab_list li.active:after{content: ""; display: block; position: absolute; left: 0; top: 0; width: 100%; height: 2px; padding-right: 1px; background: #293FEB;}
/* rs score */
.rs_score_list{padding-top:15px;}
.rs_score_list .rs_score_box{margin-top:10px;}
.rs_score_list .rs_score_box:first-child{margin-top:0;}
.rs_score_box{box-sizing:border-box;padding:15px 0;border:1px solid #e6e6e6;display:block;position:relative;overflow:hidden;}
.rs_score_box:after{content:"";display:block;position:absolute;left:50%;top:0;width:1px;height:100%;background: #e6e6e6;}
.rsb_left{box-sizing:border-box;float:left;width:50%;padding:6px 10px 0;text-align:center;}
.rsb_long{padding-top:17px;}
.rank_list_wrap {margin-top: 20px;}
.rsb_item{display:block;overflow:hidden;position:relative;padding-bottom:20px;margin-top:2vw;font-size:15px;font-weight:bold;color:#000;line-height:22px;white-space:nowrap;text-overflow:ellipsis;}
.rsb_item:after{content:"";display:block;position:absolute;left:50%;bottom:0;margin-left:-10px;width:20px;height:1px;background: #000;}
.rsb_label{display:block;margin-top:8px;font-size:16px;font-weight:normal;color:#fd5758;line-height:20px;}
.rsb_score{display:block; padding-top:20px; font-size:20px; line-height:20px; font-weight:bold;}
.rsb_score .rsb_score_sum{font-size:36px; font-weight:bold; color:#463959;}
.rsb_gray_label{margin-top:12px;font-size:14px;font-weight:normal;color:#666;line-height:18px; display:inline-block;}
.rs_score_box .score_mid_point{display:block;text-align:center;}
.rs_score_box .score_mid_ico{margin-top:7px;text-align:center;}
.rsb_right{float:right;box-sizing:border-box;width:50%;padding:15px 10px 0;}
.rsb_right dt{float:left;width:60%;padding:3px 0 2px;font-size:14px;font-weight:normal;color:#666;line-height:22px; text-align: left;}
.rsb_right dd{float:left;width:40%;padding:3px 0 2px;font-size:14px;font-weight:normal;color:#333;line-height:22px;text-align:right;}

/* score */
.score_mid_point{font-size:20px;font-weight:bold;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:28px;}
.score_mid_point span{font-size:14px;font-family:'돋움',arial,sans-serif;}
.score_big_point{font-size:32px;font-weight:bold;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:36px;}
.score_big_point span{font-size:17px;font-weight:bold;font-family:'돋움',arial,sans-serif;vertical-align: middle;}
.score_ico{display:inline-block;min-width:34px;font-size:16px;font-weight:normal;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:22px;}
.score_mid_ico{display:inline-block;font-size:14px;font-weight:normal;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:28px;}
.score_ico .ico_updown, .score_mid_ico .ico_updown{margin-right:3px;}
.score_ico.up .ico_updown, .score_mid_ico.up .ico_updown, .score_mid_ico .up .ico_updown{width:11px;height:6px;background-position:0 0;}
.score_ico.down .ico_updown, .score_mid_ico.down .ico_updown, .score_mid_ico .down .ico_updown{width:11px;height:6px;background-position:0 -10px;}
.score_big_ico{display:inline-block;font-size:32px;font-weight:bold;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:36px;}
.score_big_ico.up .ico_updown{width:19px;height:10px;background-position:-15px 0;}
.score_big_ico.down .ico_updown{width:19px;height:10px;background-position:-15px -14px;}
.score_big_ico span{font-size:17px;font-weight:bold;font-weight:bold;font-family:'돋움',arial,sans-serif;vertical-align: middle;}
.score_mid_point.up, .score_big_point.up, .score_ico.up, .score_mid_ico.up,  .score_mid_ico .up, .score_big_ico.up{color:#fd5758;}
.score_mid_point.down, .score_big_point.down, .score_ico.down,  .score_mid_ico.down,  .score_mid_ico .down, .score_big_ico.down{color:#2c42c3;}

/* icon */
.ico_updown{display:inline-block;overflow:hidden;background:url(https://img.x1.co.kr/robo/images/shinhan/mobile/common/icon_updown.png) no-repeat;background-size:34px 24px;color:transparent;vertical-align: middle;}
.ico_updown_up{width:11px;height:6px;background-position:0 0;}
.ico_updown_down{width:11px;height:6px;background-position:0 -10px;}
.ico_updownbig_up{width:19px;height:10px;background-position:-15px 0;}
.ico_updownbig_down{width:19px;height:10px;background-position:-15px -14px;}
.ico_bul{padding-left:30px;background: url(//img.x1.co.kr/robo/images/mobile/sub/ico_model_info.png) no-repeat;background-size:25px 79px;}
.ico_bul_cha{background-position:0 0;}
.ico_bul_process{background-position:0 -27px;}
.ico_bul_rule{background-position:0 -54px;}

/* rank */
.icon_rank{display:inline-block;overflow:hidden;width:30px;height:30px;background:url(//img.x1.co.kr/robo/images/mobile/main/kw_sp_rank.png) no-repeat;background-size:30px 120px;font-size:14px;font-weight:bold;color:#fff;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:24px;text-align:center;}
.ir_1{background-position:0 0;}
.ir_2{background-position:0 -30px;}
.ir_3{background-position:0 -60px;}
.ir_4, .ir_5, .ir_6, .ir_7, .ir_8, .ir_9, .ir_10{background-position:0 -90px;}

/* rank list */
.rank_list_wrap{margin-top:20px;}
.rank_list{border-bottom:1px solid #e6e6e6;display:block;position:relative;padding:11px 55px 10px 28px;}
.rank_list .rank{display:inline-block;position:absolute;left:0;top:12px;width:18px;height:18px;border:1px solid #999;font-size:14px;color:#999;font-family: 'Tahoma','돋움',arial,sans-serif;line-height:18px;text-align:center;vertical-align: middle;}
.rank_list .top .rank{border:1px solid #dc0082;color:#dc0082;}
.rank_list .item{display:block;overflow:hidden;height:23px;font-size:15px;color:#333;line-height:23px;white-space:nowrap;text-overflow:ellipsis;}
.rank_list .score_ico{position:absolute;right:0;top:12px;}

