.point_service_wrap .point_service_list img.robo_pentagon{width:145px;}
.point_service_wrap .point_service_list img.robo_cha{width:110px;}
.point_service_wrap .point_service_list img.robo_signal{width:135px}
.point_service_wrap .point_service_pagi{position:relative; text-align:center; margin-top:10px;}
.point_service_wrap .point_service_pagi .swiper-pagination-bullet-active{background:#0046FF; width:40px; border-radius:10px;}
.point_service_wrap .point_service_pagi .swiper-pagination-bullet{transition: width .5s; border-radius:10px; margin:0 2px;}

/*로보스탁 호수별 유니버스*/
