.universe_hosu_wrap h2{font-size:25px; font-weight:700; letter-spacing:-1.25px; text-align:center; margin-bottom:12px;}
.universe_hosu_wrap > p{font-size:17px; font-weight:bold; letter-spacing:-0.85px; text-align:center; line-height:normal;}
.universe_hosu_wrap > span{font-size:14px; color:#666; letter-spacing:-0.7px; text-align:center; display:block; margin-top:16.5px; font-weight:500;}
.universe_hosu_wrap .universe_hosu_list{padding:31px 16px 20px 16px;}
.universe_hosu_wrap .universe_hosu_list .point_box{padding:26px 19px 20px 19px; border:solid 2px #dee2e6; opacity:0.9; background:#fff; border-radius:15px; box-shadow: 0 8px 10px 0 rgba(0, 0, 0, 0.15);}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content h3{font-size:18px; font-weight:500; color:#1e1e1e; text-align:left; margin-bottom:14px; line-height:1.2;}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content span{font-size:12px; padding:0 5px 2px 5px; border-radius:4px; line-height:18.5px;}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content .robo_1{color:#ffe3e3; background:#fb3e3e;}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content .robo_2{color:#1db52c; background:#e1ffca;}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content .robo_3{color:#FF7500; background:#FFF1CD;}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content .robo_4{color:#ffe3e3; background:#fb3e3e;}
.universe_hosu_wrap .universe_hosu_list .point_box .point_content .swing{color:#fb4b4b; background:#ffe3e3;}
.universe_hosu_wrap .universe_hosu_list .point_img{text-align:right; margin-top:18px;}
.universe_hosu_wrap .universe_hosu_list .point_img img{width:120px;}
.universe_hosu_wrap .universu_hosu_pagi{position:relative; text-align:center; margin-top:10px;}
.universe_hosu_wrap .universu_hosu_pagi .swiper-pagination-bullet-active{background:#0046FF; width:40px; border-radius:10px;}
.universe_hosu_wrap .universu_hosu_pagi .swiper-pagination-bullet{transition: width .5s; border-radius:10px; margin:0 2px;}

/*로보스탁 프로세스*/
.robo_process_wrap{padding:42px 16px 63px 16px; background:url('https://img.x1.co.kr/robo/images/shinhan/mobile/service/process_bg.png') no-repeat 65% 62.7%; background-size:73%;}
.robo_process_wrap h2{font-size:25px; font-weight:700; letter-spacing:-1.25px; text-align:center; margin-bottom:20px;}
.robo_process_wrap .process_list h3{display:flex; align-items:center; font-size:18px; font-weight:bold; color:#1e1e1e; letter-spacing:-0.9px; justify-content:center; margin-bottom:16px;}
.robo_process_wrap .process_list h3 img{width:17px; height:17px; margin-right:7px;}
.robo_process_wrap .item_analyze{margin-bottom:55px;}
.robo_process_wrap .item_analyze p{font-size:16px; line-height:1.44; letter-spacing:-0.8px; text-align:center; font-weight:400;}
.robo_process_wrap .item_analyze .ana_img{text-align:center;}
.robo_process_wrap .item_analyze .ana_img img{width:295px;}
.robo_process_wrap .recommend_item{margin-bottom:70px;}
.robo_process_wrap .recommend_item h3{margin-bottom:29px;}
.robo_process_wrap .recommend_item ul li{background:#fff; padding:10px 0; margin-bottom:20px; border-radius:10px;}
.robo_process_wrap .recommend_item ul li:last-child{margin-bottom:0;}
.robo_process_wrap .recommend_item ul li img{width:18px; margin-right:4px;}
.robo_process_wrap .recommend_item ul li p{font-size:17px; letter-spacing:-0.85px; font-weight:normal; text-align:center; line-height:normal; display:flex; align-items:center; justify-content:center;}
.robo_process_wrap .recommend_item ul li p .color_bold{color:#f53b4e; font-weight:bold;}
.robo_process_wrap .trading_timing h3 span{background:linear-gradient(to top, #f8d4d4 40%, transparent 0%);}
.robo_process_wrap .trading_timing h3 img{width:20px; height:20px; margin-right:7px;}
.robo_process_wrap .trading_timing p{font-size:17px; letter-spacing:-0.85px; text-align:center; line-height:normal; margin-bottom:63px;}
.robo_process_wrap .trading_timing p .color_bold{font-size:20px; font-weight:bold; color:#f53b4e; margin-top:6px; display:block;}
.robo_process_wrap .trading_timing .robot{text-align:center;}
.robo_process_wrap .trading_timing .robot img{width:190px;}
.robo_process_wrap .trading_timing .robot span{display:block; color:rgba(0,0,0,0.6); font-size:15px; line-height:normal; margin-top:22px;}

/*로보스탁 사용법*/
.use_guide_wrap{padding:42px 16px 18px 16px; background:#eaebef;}
.use_guide_wrap h2{font-size:25px; font-weight:700; letter-spacing:-1.25px; text-align:center; margin-bottom:26px;}
.use_guide_wrap .use_guide_box{background:#fff; margin-bottom:8px; border-radius:10px; padding:23px 16px 7px;}
.use_guide_wrap .use_guide_box h3{font-size:19px; font-weight:bold; text-align:center;}
.use_guide_wrap .use_guide_box > span{display:block; text-align:center; color:#abadb2; font-size:14px; margin-top:5px;}
.use_guide_wrap .signal_guide{padding:23px 16px 26px;}
.use_guide_wrap .signal_guide > ul{margin-top:35px;}
.use_guide_wrap .signal_guide > ul > li{display:flex; align-items:center;}
.use_guide_wrap .signal_guide > ul > li .square{width:56px; height:56px; background:#f8f8fc; box-shadow: 1.5px 2px 2px 0 rgba(0, 0, 0, 0.15); border-radius:19px; position:relative;}
.use_guide_wrap .signal_guide > ul > li .square span{width:9.5px; height:9.5px; background:#ff715b; display:block; border-radius:50%; position:absolute; left:50%; top:50%; transform:translate(-50%,-50%);}
.use_guide_wrap .signal_guide > ul > li .first_square span:nth-child(1){top:20px; left:20px;}
.use_guide_wrap .signal_guide > ul > li .first_square span:nth-child(2){top:35px; left:35px;}
.use_guide_wrap .signal_guide > ul > li .box_list{margin-left:19px; width:70%; border-bottom:1px solid rgba(82,74,73,0.1); padding:13px 0;}
.use_guide_wrap .signal_guide > ul > li .box_list > span{font-size:18px; font-weight:bold; }
.use_guide_wrap .signal_guide > ul > li .box_list p{font-size:14px; letter-spacing:-0.7px; line-height:normal;}
.use_guide_wrap .signal_guide > ul > li .box_list ul{margin-top:8px;}
.use_guide_wrap .signal_guide > ul > li .box_list ul li{position:relative;}
.use_guide_wrap .signal_guide > ul > li .box_list ul li p{margin-left:3px;}
.use_guide_wrap .signal_guide > ul > li .box_list ul li:before{content:''; width:2px; height:2px; position:absolute; left:0; top:50%; transform:translateY(-50%); background:#000;}
.use_guide_wrap .portfolio_guide ul{margin-top:21px;}
.use_guide_wrap .portfolio_guide ul li{display:flex; align-items:center; justify-content:space-between; margin-bottom:12px;}
.use_guide_wrap .portfolio_guide ul li:last-child{margin-bottom:0;}
.use_guide_wrap .portfolio_guide ul li:last-child .portfolio_list{border-bottom:0;}
.use_guide_wrap .portfolio_guide ul li .portfolio_list{padding-bottom:12px;  border-bottom:1px solid rgba(82,74,73,0.1); width:70%;}
.use_guide_wrap .portfolio_guide ul li .portfolio_list em{color:#f53b43; font-size:14px; font-weight:bold; letter-spacing:-0.7px; line-height:normal;}
.use_guide_wrap .portfolio_guide ul li .portfolio_list span{display:block; line-height:normal; font-size:18px; letter-spacing:-0.9px; font-weight:normal;}
.use_guide_wrap .portfolio_guide ul li .portfolio_img img{width:73px;}

/*로보스탁 부가서비스*/
.addition_service_wrap{padding:42px 5px; background:#fff;}
.addition_service_wrap h2{font-size:25px; font-weight:700; letter-spacing:-1.25px; text-align:center; margin-bottom:12px;}
.addition_service_wrap > p{text-align:center; font-size:17px; font-weight:bold; line-height:normal; letter-spacing:-0.8px; margin-top:12px; color:#1e1e1e;}
.addition_service_wrap > p em{background:linear-gradient(to top, #F8d4d4 30%, transparent 30%);}
.addition_service_wrap .addition_service_list ul{display:grid; grid-template-columns:1fr 1fr; gap:4px; margin-top:40px;}
.addition_service_wrap .addition_service_list ul li .list_box{height:90px; background:#f5f6fa; border-radius:11px; box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.15); padding:19px 15px; position:relative;}
.addition_service_wrap .addition_service_list ul li .list_box p{font-size:12px; font-weight:400; line-height:1.3; letter-spacing:-1.1px; color:rgba(0,0,0,0.5); margin-bottom:4px;}
.addition_service_wrap .addition_service_list ul li .list_box span{font-size:16px; font-weight:bold; letter-spacing:-0.8px;}
.addition_service_wrap .addition_service_list ul li .list_box img{width:75px; position:absolute; right:0; bottom:0;}

/*로보스탁 투자원칙 안내*/
.invest_guide_wrap{padding:20px 8px;}
.invest_guide_wrap h3{font-size:16px; font-weight:bold; color:#1e1e1e; text-align:center;}
.invest_guide_wrap .invest_guide_list{margin-top:20px;}
.invest_guide_wrap .invest_guide_list li{position:relative;}
.invest_guide_wrap .invest_guide_list li:before{content:''; position:absolute; display:block; width:3px; height:3px; border-radius:50%; background:#666; left:5px; top:8px;}
.invest_guide_wrap .invest_guide_list li p{letter-spacing:-0.6px; font-weight:400; font-size:12px; color:rgba(0,0,0,0.7); line-height:normal; margin-left:15px;}
.invest_guide_wrap .invest_guide_list li p em{color:#000;}

