footer .footer_info div p{font-size:11px; line-height:1.48; letter-spacing:-0.55px; text-align:center; font-weight:300; color:#fff;}
footer .footer_info .info_1{margin-bottom:16px;}
footer .footer_info .info_1 p{font-size:13px;}
footer .footer_info .info_2{margin-bottom:16px;}

